






import Request from './class';
import * as util  from './helpers/util';
import {
	defaultsWB,
	defaultsZT
} from './defaults';


function createInstance(config) {
    let context = new Request(config);
    let instance = util.bind( Request.prototype.request , context );
    util.extend( instance , Request.prototype , context );
    util.extend( instance , context );
    return instance;
}

let requestWB = createInstance(defaultsWB);
let requestZT = createInstance(defaultsZT);


// 用于创建多个实例
requestWB.create = function (config) {
    return createInstance(utils.merge(defaultsWB, config));
};

// 并发请求数据处理
requestWB.spread = function (callback){
    return function (...arg) {
        return callback.apply(null , [...arg] );
    }
}

// 用于创建多个实例
requestZT.create = function (config) {
    return createInstance(utils.merge(defaultsZT, config));
};

// 并发请求数据处理
requestZT.spread = function (callback){
    return function (...arg) {
        return callback.apply(null , [...arg] );
    }
}


export {
	requestWB,
	requestZT
}; 


